*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --color-background: #40a3ff;
}

body{
  font-family: 'Montserrat',sans-serif;
}

.app{
  background-color: var(--color-background);
  transition: 0.4s ease-out;
}

.app.warm{
  background-color: var(--color-background); 
}

main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom,
  rgba(0,0,0,0.2),rgba(0,0,0,0.5));
  padding: 0px 25px;
}

.search-box{
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 20px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255,255,255,0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -5px;
  box-shadow: 0px 5px rgba(0,0,0,0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.4s ease;
}

.search-box .search-bar:focus{
  background-color: rgba(255,255,255,0.75);
}

.location-box .location{
  color: white;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}

.location-box .date{
  color: white;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50,50,70,0.5);
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display: inline-block;
  color: white;
  font-size: 100px;
  font-weight: bold;
  text-shadow: 3px 6px rgba(50,50,70,0.5);
  text-align: center;
}

.weather-box .weather{
  color: white;
  font-size: 48px;
  text-shadow: 3px 6px rgba(50,50,70,0.5);
}

img{
  width: 300px;
  height: 300px;
  object-fit: cover;
}

button{
  width: 100%;
  display: block;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255,255,255,0.5);
  border-radius: 16px 16px 0px 0px;
  box-shadow: 5px 5px rgba(0,0,0,0.2);
  margin-top: 25px;
  color: #313131;
  font-size: 20px;
}

button:hover {
  background-color: rgba(255,255,255,0.75);
  color: #313131;
  cursor: pointer;
  padding-top: 15px;
  transition: 0.8s ease;
}

